/* General styles for responsive images */
.responsive-image {
    width: 100%;
    height: auto;
}

.logo {
    max-width: 150px; /* Ajusta el tamaño del logotipo en la cabecera */
    height: auto;
    clip-path: circle(50%); /* Aplica clip-path para darle una forma circular */
}

.footer-logo {
    max-width: 100px; /* Ajusta el tamaño del logotipo en el pie de página */
    height: auto;
    clip-path: circle(50%); /* También aplicamos clip-path al logotipo en el pie de página */
}

/* Estilos para el pie de página */
footer {
    text-align: center;
    padding: 20px;
    background-color: #f1f1f1;
}

footer ul {
    list-style-type: none;
    padding: 0;
}

footer li {
    display: inline;
    margin: 0 10px;
}

footer a {
    text-decoration: none;
    color: #333;
}

/* Estilos para el encabezado */
header {
    text-align: center;
    padding: 20px;
}

header h1 {
    font-size: 2em;
    margin-top: 10px;
}

header p {
    font-size: 1.2em;
}

/* Ajustes para la navegación */
nav ul {
    display: flex;
    justify-content: center;
    list-style-type: none;
    padding: 10px;
}

nav li {
    margin: 0 15px;
}

nav a {
    text-decoration: none;
    color: #333;
    font-size: 1.1em;

}

@media (max-width: 768px) {
    .logo, .footer-logo {
        max-width: 120px;
    }

    nav ul {
        flex-direction: column;
        align-items: center;
    }

    header h1 {
        font-size: 1.5em;
    }

    header p {
        font-size: 1em;
    }
}
